h3 {
    letter-spacing: 2px;
    font-weight: bold;
}
img {
    width: 400px;
    height: 420px;    
}
.profile {
    text-align: justify;
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 2fr;
    margin-top: 40px;
}
.aboutText {
    font-size: .95em;
    line-height: 1.5;
    text-align: justify;
}
.oct {
    margin: 0 auto;
}
.oct,.oct div{
   transform:rotate(-90deg);
   width:280px;
   height:280px;
   overflow: hidden;
}
.oct div{
   transform:rotate(45deg);
}
.oct div div {
   background: url(../../../assets/images/ces2.png);
   background-size: contain;
}
.oct ~ p {
    margin-top: 30px;
}
.aboutMe h3 {
    text-align: center;
    padding: 10px 15px;    
    text-transform: uppercase;
    margin-bottom: 15px;
    box-shadow: 10px 10px 20px #00e6e6 inset;
}
.aboutMe {
    margin: 20px;
}
.skills li {
    list-style-type: none;
    padding: 10px;
    box-shadow: 5px 10px 18px #00cccc;
}
.skills {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: (4, 1fr);
}
.skillsIcon {
    max-width: 28px;
    height: auto;
}
@media only screen and (max-width: 1200px){
    img {
        width: 500px;
    }
}
@media only screen and (max-width: 1023px){
    .skills li {
        padding: 3px;
        font-size: 0.75rem;
    }
    .oct, .oct div{
        width:200px;
        height:200px;
    }
}
@media only screen and (max-width: 800px){
    .aboutText {margin: 0 15px;}
}
@media only screen and (max-width: 768px) {
    .profile{grid-template-columns: 1fr;}
}