footer {
    height: 180px;
    background-color: #1B242F;
    padding: 60px;
    margin: 0 auto;
    text-align: center;
    color: #a6a6a6;
}
.row {
    max-width: 1140px;
    margin: 0 auto;
    text-align: center;
}
.copyright{
    color: #ff0066;
    font-weight: normal;
}
footer a div {
    padding-top: 15px;
    width: 55px;
    height: 55px;
    display: inline-block;
    background-color: #253241;
}
footer a{
    text-align: center;
    width: 85px;
    height: 55px;
    margin: 0 10px;
}
footer .icon{
    color: #fff;
    font-size: 1.2em;
    max-width: 100%;
    width: 24px;
    height: auto;
}
footer h6 {
    margin-top: 20px;
    text-transform: uppercase;
    letter-spacing: 1.8px;
    color: #a6a6a6;
    text-align: center;
}
footer a:hover,
footer a div:hover {
    background-color:#00cccc;  
}
@media only screen and (max-width: 480px){
    footer a div {
        padding-top: 8px;
        width: 35px;
        height: 35px;
    }
    footer a {
        margin: 0 5px;
    }
    footer h6 {
        font-size: 80%;
        letter-spacing: 0;
    }
}
