
.introContacts {
    margin-top: 50px;
    height: 60px;
    padding: 30px;
    text-align: center;
}
.introContacts a div {
    width: 55px;
    height: 55px;
    display: inline-block;
    margin: 0 20px;
    box-shadow: 5px 5px 8px #00e6e6;
}
.introContacts a div:hover {
    box-shadow: 5px 10px 30px#03fcfc;
    font-size: 1.3em;
}
.introContacts a{
    text-align: center;
    width: 85px;
    height: 55px;
    margin: 0 10px;
}
.icon{
    color:#fff;
    width: 24px;
    height: 26px;
    padding-top: 15px;
}
@media only screen and (max-width: 767px) {
    .introContacts a div {
        font-size: 0.8em;
    }
    .intro-contacts a {
        margin: 0 5px;
        top: 60px;
    }
}
@media only screen and (max-width: 480px) {
    .introContacts {
        margin-top: 15px;
    }
    .introContacts a div {
        height: 45px;
        width: 45px;
        margin: 0 5px;
    }
}

