.showcase {
    display: flex;
    flex-wrap: wrap;
}
section {
	clear: both;
	padding: 60px;
	margin: 0px;
}
.row {
    max-width: 1140px;
    margin: 0 auto;
    text-align: center;
}
h2 {
    font-size: 180%;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    line-height: 1px;
    margin-top: 50px;
}
.BG {
    background-color: #ccffff;
}
.headerBar {
    text-align: center;
    color: #ff0066;
    font-size: 4.5em;
    margin-bottom: 50px;
}
@media only screen and (max-width: 1023px){
    section{padding: 60px 0;}
}
@media only screen and (max-width: 767px) {
    section {padding: 30px 0;}
    .row {padding: 0 4%;}
}
@media only screen and (max-width: 480px) {
    section{padding: 25px 0;}
}
