.hexagon {
    width: 100px;
    height: 55px;
    margin: 0 auto;
    background:  #00cccc;
}
.hexagon:before {
    content: "";
    position: absolute;
    top: -25px;
    left: 0;
    width: 0;
    height: 0;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    border-bottom: 25px solid #00cccc;
}
.hexagon:after {
    content: "";
    position: absolute;
    bottom: -25px;
    left: 0;
    width: 0;
    height: 0;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    border-top: 25px solid  #00cccc;
}
.icon {
    margin-top: 10px;
    color:#fff; 
    font-size: 180%;
    z-index: 1;
}
.hexaDesc p {
    font-size: 80%;
    margin-bottom: 25px;
 }
 .hexaDesc h3{
     margin-top: 50px;
     margin-bottom: 20px;
     line-height: 0.25;
 }
 @media only screen and (max-width: 480px) {
     .icon{text-align: center;}
     
 }