.BG {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
}
.introBox {
    background-color: #000;
    overflow: hidden;
    height: calc(100vh - 8em);
    padding: 6em;
    margin: 0 auto;
}
.introBoxText {
    position: absolute;
    text-align: center;
    width: 30em;    
    top: 40%;
    left: 50%; 
    transform: translate(-50%, -50%); 
}
.introBoxText h3, .introBoxText h1{
    margin: 40px auto;
    color:#fff;   
    transform: translateY(-50%);   
    font-size: 1.6em;
}
.introBoxText h1 {
    text-transform: uppercase;
    color: #00e6e6;    
    font-size: 280%;
    letter-spacing: 0.1em;   
    width: 250px;
    padding: 10px 0;
    margin-bottom: 1.5em;
    box-shadow: 5px 10px 20px #00e6e6;
}
.viewWork a {
    position: absolute; 
    font-size: 1em;   
    top: 150%;
    transform: translate(-50%, -50%); 
    cursor: pointer;
    padding: 1.2em;
    box-shadow: 3px 5px 10px #00e6e6;
    color: #fff;
}
.viewWork a:hover {
    font-weight: bold;
    font-size: 1.1em;
    box-shadow: 3px 10px 60px #00e6e6;
}

@media only screen and (min-width: 1366px){
    .viewWork a {
        top: 130%;
    }
}
@media only screen and (min-width: 1024px) {
    .viewWork a {
        top: 130%;
    }
}

@media only screen and (max-width: 768px) {
    .viewWork a {
        top: 140%;
    }
}
@media only screen and (max-width: 480px) {
    .introBoxText h1{
        font-size: 180%;
    }
    .introBoxText h3{
        font-size: 95%;
    }
    .viewWork a{
        font-size: 95%;
        top: 130%;
        padding: 10px;
    }
    .viewWork a:hover {
        font-size: 0.9em;
    }
}
