html, 
body {
    background-color: #fff;
    color:#444649;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    font-size: 20px;
    text-rendering: optimizeLegibility;
    overflow-x: hidden;
}
.clearfix { zoom: 1; } /* for IE6 support */
* {
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
*:focus {
    outline: none;
}
.clearfix:after{
    content: '.';
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
}
/*----------LINKS-----------*/

a:link,
a:visited {
    color:  #fff;
    text-decoration: none;
    padding-bottom: 20px;
    transition: border-bottom 0.2s, color 0.2s;
}
@media only screen and (max-width: 1200px){
    .introBoxText {
        width:100%;
        padding: 0 2%;
    }
}
@media only screen and (max-width: 1023px){
    body{font-size: 18px;}
}
@media only screen and (max-width: 767px) {
    h1{font-size: 180%;}
}
