
:root {
  --primary-color: rgba(13, 110, 139, 0.75);
  --overlay-color: rgba(24, 39, 51 , 0.8);
  --menu-speed: 0.75s;
}  
.navBrand span {
  color: #ff0066;
}
.navBrand small {
  display:block;
  color: #fff;
  font-weight: normal;
}
.navBrand{
  float: left;
  display: block;
  margin: 10px 10px 20px 20px;
  height: 15px;
  color: #ff0066;
  font-size: 1rem;
  font-weight: bold;
  letter-spacing: 1.2px;
}
nav {
  display: block;
  float: right;
}
.mainNav li, .mainNav li a {
  display: inline-block;
  cursor: pointer;
  list-style: none;
  color: #fff;
  padding: 8px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
}
.mainNav li a:active,
.mainNav li a:hover {
  color: #ff0066;
  border-bottom: 2px solid  #ff0066;
}
.menuWrap {
  z-index: 1;
  position: fixed; 
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  padding: 0.3rem;
  background-color:rgba(27, 36, 47, 0.92);
  box-shadow: 0 2px 2px  #ff0066;
}

.menuWrap .toggler {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  cursor: pointer;
  width: 50px;
  height: 50px;
  opacity: 0;
  display: none;
}

.menuWrap .hamburger {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 70px;
  height: 70px;
  padding: 0.9rem;
  display: none;
  align-items: center;
  justify-content: center;
}

/* Hamburger Line */
.menuWrap .hamburger > div {
  position: relative;
  flex: none;
  width: 100%;
  height: 2px;
  background: #fff;

  display: none;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease;
  cursor: pointer;
}

/* Hamburger Lines - Top & Bottom */
.menuWrap .hamburger > div::before,
.menuWrap .hamburger > div::after {
  content: '';
  position: absolute;
  z-index: 1;
  top: -10px;
  width: 100%;
  height: 2px;
  background: inherit;
  display: none;
}

/* Moves Line Down */
.menuWrap .hamburger > div::after {
  top: 10px;
  display: none;
}

/* Toggler Animation */
.menuWrap .toggler:checked + .hamburger > div {
  transform: rotate(135deg);
}

/* Turns Lines Into X */
.menuWrap .toggler:checked + .hamburger > div:before,
.menuWrap .toggler:checked + .hamburger > div:after {
  top: 0;
  transform: rotate(90deg);
}

/* Rotate On Hover When Checked */
.menuWrap .toggler:checked:hover + .hamburger > div {
  transform: rotate(225deg);
}

/* Show Menu */
.menuWrap .toggler:checked ~ .menu {
  visibility: visible;
}

.menuWrap .toggler:checked ~ .menu > div {
  transform: scale(1);
  transition-duration: var(--menu-speed);
}

.menuWrap .toggler:checked ~ .menu > div > div {
  opacity: 1;
  transition:  opacity 0.4s ease 0.4s;
}
.menuWrap .menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menuWrap .menu > div {
  background: var(--overlay-color);
  border-radius: 50%;
  width: 200vw;
  height: 200vw;
  display: flex;
  flex: none;
  align-items: center;
  justify-content: center;
  transform: scale(0);
  transition: all 0.4s ease;
}
.menuWrap .menu > div > div {
  text-align: center;
  max-width: 90vw;
  max-height: 100vh;
  opacity: 0;
  transition: opacity 0.4s ease;
}
.menuWrap .menu > div > div > ul > li {
  list-style: none;
  color: #fff;
  font-size: 1.2rem;
  padding: 15px;
  text-align: center;
  letter-spacing: 1.5px;
  display: block;
  cursor: pointer;
  width: 150px;
  height: 55px;
  margin: 40px;
  background-color:rgba(22, 10, 19, 0.92);
  box-shadow: 5px 5px 8px #00e6e6;
}
.menuWrap .menu > div > div > ul > li:hover{
  box-shadow: 5px 10px 30px#03fcfc;
  font-size: 1.35rem;
  color: #03fcfc;
}
.menuWrap .menu > div > div > ul > li > a {
  color: inherit;
  text-decoration: none;
  transition: color 0.4s ease;
}
@media only screen and (max-width: 1023px){
  .menuWrap .menu > div > div > ul > li {
    font-size: 1rem;
  }
  .menuWrap .menu > div > div > ul > li:hover{
    font-size: 1.15rem;
  }
}
@media only screen and (max-width: 800px) {
  .mainNav li {
    font-size: 0.7rem;
  }
  nav {
    display: none;
  }
  .menuWrap .toggler {
    display: block;
  }
  .menuWrap .hamburger {
    display: flex;
  }
  .menuWrap .hamburger > div {
    display: flex;
  }
  .menuWrap .hamburger > div::before,
  .menuWrap .hamburger > div::after {
    display: block;
  }
}
@media only screen and (max-width: 480px){
  .navBrand {
    font-size: 0.9rem;
    margin-top: 6px;
  }
  .navBrand small {
    font-size: 90%;
  }
}
@media only screen and (max-width: 320px){
  .navBrand {
    font-size: 0.8rem;
    margin-top: 5px;
  }
  .navBrand small {
    font-size: 80%;
  }
}



