.BG {
    text-align: center;
    line-height: 1.4;
}
section {
	clear: both;
	padding: 60px;
	margin: 0px;
}
.row {
    max-width: 1140px;
    margin: 0 auto;
    text-align: center;
}
h2 {
    font-size: 180%;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    line-height: 1px;
    margin-top: 50px;
}
.headerBar {
    text-align: center;
    color: #ff0066;
    font-size: 4.5em;
    margin-bottom: 50px;
}
@media only screen and (max-width: 1200px){
    .row{padding: 0 2%;}
}
@media only screen and (max-width: 1023px){
    section { padding: 60px 0;}
    .row { padding: 0 4%;}
    section { padding: 60px 0;}
}
@media only screen and (max-width: 767px) {
    h2{font-size: 150%;}
}
