.contents {
    text-align: center;
    display: grid;
    gap: 20px;  
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
}
@media only screen and (max-width: 1200px){
    .contents {
        grid-template-columns: 1fr 1fr;
    }
}
@media only screen and (max-width: 767px) {
    .contents{
        gap: 10px;
        grid-template-columns: 1fr 1fr;
    }
}