.row {
    max-width: 1140px;
    margin: 0 auto;
    text-align: center;
}
.blogHeader {
    line-height: 2em;
}
.blogHeader>h3 {
    font-size: 1.5em;
    color: #fff;
}
.blogHtml p {
    line-height: 1.5;
    text-align: justify;
    margin-top: 1em;
}
.blogHtml h4 {
    text-align: center;
    line-height: 2.5;
}
.blogHtml h3 {
    margin-top: 15px;
}
.blogHtml .content h4 {
    text-align: justify;
    margin-top: 1.5em;
    text-transform: capitalize;
}
.blogHtml .content p {
    text-align: justify;
    margin: 1.5em;
}
.blogHtml img{
    width: 80%;
    max-width: 600px;
    max-height: 420px;
    margin: 2em;
}
footer, .blogHeader {
    height: 130px;
    background-color: #1B242F;
    padding: 30px;
    margin-top: 20px;
    text-align: center;
    color: #a6a6a6;
}
.blogFooter {
    height: 70px;
    padding: 15px;
    margin-top: 30px;
}
footer a div {
    width: 55px;
    height: 55px;
    display: inline-block;
    background-color: #253241;
}
footer a{
    width: 85px;
    height: 55px;
    margin: 10px;
}
footer i{
    color: #fff;
    width: 24px;
    height: 26px;
    margin: 16px auto;
}
footer h5 {
    margin-top: 15px;
    text-transform: uppercase;
    letter-spacing: 1.8px;
    color: #a6a6a6;
}
@media only screen and (max-width: 320px){
    .blogHeader>h3{font-size: 90%;}
    .row {font-size: 90%;}
    .blogHtml img {
        width: 200px;
        height: auto;
    }
    footer, .blogHeader{
        padding: 15px;
        height: 130px;
    }
    .blogFooter {
        padding: 5px;
        margin-top: 10px;
    }
}
