.BG {
    background-color:#253241;
    margin: 0;
    padding: 0;
    text-align: center;
    color: #fff;
}
section {
	clear: both;
	padding: 60px;
	margin: 0px;
}
.row {
    max-width: 1140px;
    margin: 0 auto;
    text-align: center;
}
h2 {
    font-size: 180%;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    line-height: 1px;
    margin-top: 50px;
}
input {
    width: 500px;
    height: 38px;
    margin: 0 0 4px;
    padding: 10px 15px;
    color: #fff;
    background-color: #1B242F;
    box-shadow: 3px 5px 10px #849191;
    font-size: 0.8rem;
    border: none;
}
textarea {
    width: 500px;
    height: 150px;
    margin: 0 0 5px;
    padding: 10px 15px;
    color: #fff;
    background-color: #1B242F;
    box-shadow: 3px 5px 10px #849191;
    font-size: 0.8rem;
    border: none;
}
button {
    padding: 8px 35px;
    margin-top: 50px;
    margin-bottom: 150px;
    background-color: #1B242F;
    color: #fff;
    text-transform: uppercase;
    font-size: 0.9rem;
    box-shadow: 3px 5px 10px #849191;
    cursor: pointer;
}
.headerBar {
    text-align: center;
    color:  #ff0066;
    font-size: 4.5em;
    margin-bottom: 50px;
}
@media only screen and (max-width: 1200px){
    .row { padding: 0 2%; }
}
@media only screen and (max-width: 1023px){
    section { padding: 60px 0;}
}
@media only screen and (max-width: 767px) {
    section { padding: 30px 0; }
    .row { padding: 0 4%;}
    h2 { font-size: 150%; }
    input, textarea{
        width: 85%;
    }
}
@media only screen and (max-width: 480px) {
    input[type=submit]{
        text-align: center;
    }
    form{width: 100%;}
}