.modalContent {
    box-sizing: border-box;
    background: #fff;
    max-width: 100vw;
    height: auto;
    width: 100%;
    position: relative;
    justify-content: left;
}
.closeModal {
    position: absolute;
    top: -50px;
    right: -8px;
    width: 30px;
    height: 30px;
    text-indent: -9999px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url('data:image/png;base64,');
}
img {
    max-height: 310px;
    max-width: 420px; 
    width: 100%;
    margin-bottom: 20px;
}
h4 {
    margin-bottom: 10px;
    color: #1B242F;
}
.headerBar {
    position: relative;
    color: #1B242F;
    font-size: 3.8em;
    line-height: 0.2;
    margin-bottom: 30px;
}
p {
    font-size: .8rem;
}
.col {
    display: block;
    float:left;
    text-align: center;
	margin: 1% 0 1% 1.6%; 
}
.span1of2 {
    width: 49.2%;
}

.col:first-child { margin-left: 0; } /* all browsers except IE6 and lower */

@media only screen and (max-width: 767px) {
    img{
        max-width: 400px;
        max-height: 200px;
        width: 100%;
    }
    .col { 
        width: 100%;
        margin: 0 0 4% 0;
    }
}

/*  REMOVE MARGINS AS ALL GO FULL WIDTH AT 480 PIXELS */
@media only screen and (max-width: 480px) {
	.col { 
		margin: 1% 0 1% 0%;
        margin: 0;
    }   
    h4, h6 {
        font-size: 95%;
    }
    h4, h6, p{
        text-align: left;
    }
}
@media only screen and (max-width: 320px) {
    .modalContent {
       font-size: 0.8em;
    }
}


